import * as React from "react"
import ImageSpread from './image-spread'
import type { ImageOption } from './image-with-options'
import {
  renderOverlay,
  renderOptionsButton,
  useShouldShowOptions,
} from './image-overlay-utils'

interface ImageSpreadWithOverlayProps {
  className?: string
  style?: React.CSSProperties
  overlaySvg?: string
  overlayClassName?: string
  options?: ImageOption[]
  dialog?: Record<string, any> | null
  dialogProps?: Record<string, any>
  spreadProps: any
  children?: React.ReactNode
}

const ImageSpreadWithOverlay = ({
  overlaySvg,
  overlayClassName,
  options = [],
  dialog,
  spreadProps,
  children,
}: ImageSpreadWithOverlayProps) => {
  const showOptions = useShouldShowOptions(options)

  // Render spread with overlay inside the image container
  // Pass dialog prop to ImageSpread so click-to-edit targets the image area
  // No relative wrapper - it breaks absolute positioning!
  return (
    <>
      <ImageSpread {...spreadProps} dialog={dialog}>
        {renderOverlay(overlaySvg, overlayClassName)}
        {children}
      </ImageSpread>
      {renderOptionsButton(options, showOptions)}
    </>
  )
}

export default ImageSpreadWithOverlay
